* {
  font-family: 'Roboto', sans-serif; }

header {
  position: relative; }

nav {
  z-index: 100;
  display: block;
  position: fixed;
  width: 100%;
  height: 50px;
  background-color: #212121; }

span {
  color: #1EACC7; }

.menu {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  list-style: none; }

.menu_item {
  margin-top: 12px; }

.menu {
  .menu_link {
    font-style: normal;
    font-weight: 300;
    color: #FFFFFF; }

  .menu_item {
    border-right: 2px dotted black;
    padding-right: 34px; } }

.subheader {
  margin-top: 50px;
  width: 100%;
  display: block;
  position: absolute;
  height: 87px;
  background-color: rgba(0, 0, 0, 0.6);

  .subheader_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }

  .subheader_descr {
    margin-top: 1px;
    font-weight: 200;
    font-size: 13px;
    color: #FFFFFF;
    opacity: 0.6; }

  .subheader_call {
    margin-top: 24px;
    font-weight: 300;
    font-size: 12px;
    line-height: 14px;
    text-align: right;
    color: #FFFFFF; }

  .subheader_phone {
    font-size: 22px;
    text-transform: uppercase;
    color: #FFFFFF;
    font-weight: 400; }

  .subheader_btn {
    width: 172px;
    height: 46px;
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    border: none;
    font-size: 14px;
    text-transform: uppercase;
    color: #FFFFFF;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
    margin-top: 20px; } }

.promo {
  padding: 200px 0 135px 0;
  min-height: 800px;
  background: url('../img/uber_bg.jpg') center (center / cover) no-repeat;

  .promo_header {
    font-weight: 700;
    font-size: 60px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    line-height: 60px; }

  .promo_subheader {
    font-weight: 700;
    font-size: 36px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    line-height: 36px;
    margin-top: 22px; }

  .promo_descr {
    font-size: 18px;
    text-align: center;
    color: #FFFFFF;
    margin-top: 23px; }

  .promo_btn {
    display: block;
    margin: 118px auto 0 auto;
    width: 259px;
    height: 63px;
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    border: none;
    font-size: 18px;
    text-transform: uppercase;
    text-align: center;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
    color: #FFFFFF; } }

.card {
  display: flex;
  margin: 0 auto;
  margin-top: 20px;
  background-color: black;
  width: 113px;
  height: 24px;
  font-size: 11px;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  color: white; }

.main_item {
  margin-bottom: -24px;
  font-weight: 700;
  font-size: 38px;
  text-align: center;
  text-transform: uppercase; }

.promo_logos {
  min-height: 850px;
  display: flex;

  .photo_wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 116px;
    min-width: 116px;
    height: 116px;
    background-color: #1EACC7;
    border-radius: 100%; }

  .main_wrapper {
    display: flex;
    margin-top: 40px;

    .main_text {
      display: flex;
      flex-direction: column;
      font-size: 17px;
      line-height: 21px;
      margin-left: 25px; } } }
